@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  height: 100%;
  background: linear-gradient(
    to bottom right,
    #002233,
    10%,
    #00cc99,
    85%,
    #002233
  );
}

body {
  font-size: 1.2em;
}

.App {
  width: 90vw;
  margin: 40px auto;
  color: #002233;
}

.h1 {
  margin-bottom: 2.4rem;
  color: #002233;
  font-weight: 600;
}

.center {
  text-align: center;
}

.content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
}

.figure {
  width: 44vw;
  margin: 0;
}

.figure img {
  width: 100%;
}

.react-player {
  width: 40vw;
}

.description {
  width: 42vw;
  margin-left: 2vw;
}

.explanation::first-line {
  font-weight: bold;
  font-size: 1.2em;
  text-align: left;
}

@media (max-width: 1392px) {
  .content {
    width: 80vw;
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
  }

  .react-player {
    width: 80vw;
    margin: 0 auto;
  }

  .figure,
  .description {
    width: 80vw;
  }

  .description {
    margin-top: 2rem;
  }
}

